.checkout-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--main-bg-color-1);
  border-left: .3rem solid var(--accent-color-1);
  z-index: 9;
  justify-self: start;
  height: 100%;
  color: var(--main-bg-color-2);
}

.checkout-header-container {
  display: grid;
  grid-template: auto / auto;
  align-items: center;
  justify-items: center;
  font-size: 2rem;
  font-weight: 700;
  background-color: var(--main-bg-color-1);
  color: black;
  width: 100%;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.checkout-headline {
  width: max-content;
  height: min-content;
  font-weight: 700;
}

.checkout-header-amount-items {
  font-size: 1rem;
  color: rgb(243, 243, 243);
  background-color: gray;
  border-radius: .5rem;
  padding-left: .5rem;
  padding-right: .5rem;
  font-weight: 400;
}

.checkout-articles-container {
  width: 70%;
}

.checkout-article-list {
  display: grid;
  grid-template: auto / 2fr 1fr;
  width: 100%;
  align-items: center;
  border-bottom: .2rem solid var(--main-bg-color-2);
  padding-bottom: 1rem;
  min-height: 55vh;
}

.checkout-payment-method-container {
  position: sticky;
  position: -webkit-sticky;
  bottom: 200%;
  grid-area: 1 / 2 / 1 / 3;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.checkout-button-container {
  display: flex;
  gap: 2rem;
}

.checkout-button {
  padding: 1.5rem;
  font-size: 1.5rem;
  background-color: var(--main-bg-color-1);
  border: .2rem solid var(--main-bg-color-2);
  color: var(--main-bg-color-2);
  border-radius: .5rem;
  cursor: pointer;
}

.paypal-logo {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  font-size: 1.2rem;
  border: .1rem solid var(--main-bg-color-2);
  border-radius: .5rem;
  padding: 1rem;
  width: 90%;
  cursor: pointer;
}

.checkout-credit-cards-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: .5rem;
  width: 100%;
}

.credit-card-img {
  height: 2rem;
}

select {
  height: 2rem;
  font-size: 1.5rem;
  border-radius: .25rem;
  border: .1rem solid var(--main-bg-color-2);
}

.select:active {
  border: .1rem solid var(--accent-color-1);
  outline: .1rem solid var(--accent-color-1);;
}

.checkout-input {
  font-size: 1.5rem;
  height: 2rem;
  border-radius: .25rem;
  outline: none;
  border: .1rem solid gray;
  border-bottom: .2rem solid var(--main-bg-color-2);
}

.checkout-input:focus {
  border: .1rem solid var(--accent-color-1);
  outline: .1rem solid var(--accent-color-1);
  border-bottom: .2rem solid var(--main-bg-color-2);
}

.checkout-expiration-date {
  display: flex;
  text-align: center;
  justify-content: center;
  gap: 2rem;
}

.crypto-payment {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.crypto-payment-img {
  width: 10%;
  cursor: pointer;
}

.checkout-articles {
  display: flex;
  flex-direction: column;
  grid-area: 1 / 1 / 1 / 1;
  align-items: center;
  /* border: .2rem solid var(--main-bg-color-2);
  border-radius: .5rem; */
  max-height: 70vh;
  width: 90%;
  overflow-y: scroll;
}

.checkout-item {
  display: grid;
  grid-template: auto / 1fr 1fr;
  align-items: center;
  justify-items: center;
  border-bottom: .2rem solid var(--main-bg-color-2);
  color: var(--main-bg-color-2);
  /* border-radius: 1rem; */
  padding-top: 1rem;
  padding-bottom: 1rem;
  width: 70%;
}

.checkout-image-container {
  display: flex;
  justify-content: center;
  height: 30vh;

}

.checkout-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: 0% 50%;
}

.checkout-product-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  gap: 1rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.checkout-product-details-selections {
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

.checkout-product-details-selections-titles {
  font-size: 1.25rem;
}

.checkout-product-color {
  font-size: 1rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  text-align: center;
}

.checkout-product-size {
  font-size: 1rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  text-align: center;
}

.checkout-product-size {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  text-align: center;
}

.checkout-product-amount-container {
  position: relative;
  display: flex;
  gap: .5rem;
  align-items: center;
  justify-items: center;
  align-content: center;
  justify-content: center;
  font-size: 3rem;
}

.checkout-product-amount-control {
  position: relative;
  bottom: .5vh;
  cursor: pointer;
  width: 2rem;
  font-size: 2rem;
}

.checkout-product-amount {
  padding: .25rem;
  padding-left: 1rem;
  padding-right: 1rem;
  height: 2rem;
  width: 1.7rem;
  border: .15rem solid var(--accent-color-1);
  border-radius: .5rem;
  background-color: var(--main-bg-color-1);
  text-align: center;
  font-size: 1.5rem;
}

.checkout-product-amount:focus {
  outline: .15rem solid var(--accent-color-1);
  border-radius: .5rem;
}

.checkout-dollar-sign {
  color: var(--accent-color-2);
}

.checkout-remove-product {
  align-self: center;
  font-size: 1rem;
  padding: 0.5rem;
  width: fit-content;
  align-self: center;
  background-color: var(--main-bg-color-2);
  border: .1rem solid var(--main-bg-color-2);
  color: var(--main-bg-color-1);
  border-radius: 0.5rem;
  cursor: pointer;
  align-self: center;
}

.checkout-remove-product:hover {
  background-color: var(--main-bg-color-1);
  color: var(--main-bg-color-2);
  border-color: var(--main-bg-color-2);
}

.checkout-remove-product:focus {
  background-color: var(--main-bg-color-1);
  color: var(--main-bg-color-2);
  border-color: var(--main-bg-color-1);
}

.checkout-lower-container {
  display: grid;
  grid-template: auto auto / 1fr;
  align-items: center;
  background-color: var(--main-bg-color-1);
  color: black;
  width: 100%;
}

.checkout-shipping-fee {
  display: grid;
  grid-template: 1fr / 5fr 1fr;
  width: 100%;
  align-items: center;
  justify-items: right;

  padding-top: 2rem;
  color: var(--main-bg-color-2);
  font-weight: 600;
  font-size: 1.5rem;
}

.checkout-shipping-fee-amount {
  font-weight: 700;
  background-color: var(--main-bg-color-1);
  color: var(--main-bg-color-2);
  border-radius: .5rem;
  padding: .5rem;
}

.checkout-total-price-container {
  display: grid;
  grid-template: 1fr / 5fr 1fr;
  width: 100%;
  align-items: center;
  justify-items: right;
  padding-top: 2rem;
  color: var(--main-bg-color-2);
  font-weight: 600;
  padding-bottom: 1rem;
  border-bottom: .2rem solid var(--main-bg-color-2);
}

.checkout-total-price-amount {
  font-size: 3rem;
  font-weight: 700;
  background-color: var(--main-bg-color-1);
  border: .2rem solid var(--accent-color-2);
  color: var(--main-bg-color-2);
  border-radius: .5rem;
  padding: .5rem;
}

.checkout-complete-purchase {
  margin-top: 1rem;
  padding: 1.5rem;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  color: var(--main-bg-color-1);
  border: .2rem solid var(--main-bg-color-2);
  background-color: var(--accent-color-2);
  border-radius: 1rem;
  margin-bottom: 3rem;
  cursor: pointer;
  width: 30%;
}

.checkout-complete-purchase:hover {
  background-color: var(--accent-color-2);
  color: var(--main-bg-color-2);
  border: .2rem solid var(--main-bg-color-1);
}

.checkout-complete-purchase:active {
  background-color: var(--accent-color-1);
  color: var(--main-bg-color-1);
  border: .2rem solid var(--main-bg-color-1);
}

.checkout-link {
    text-decoration: none;
    width: 70%;
}

.checkout-link:hover {
  color: var(--main-bg-color-2);
}

.checkout-no-items {
  grid-area: 1 / 1 / 3 / 3;
  color: var(--main-bg-color-2);
  font-size: 2rem;
  font-weight: 700;
  border-bottom: .2rem solid var(--main-bg-color-2);
  margin-bottom: 3rem;
}


/* ---- Animations ---- */

@media (prefers-reduced-motion: no-preference) {
  .checkout-credit-cards-container {
    animation: Payment-animation 1 .5s;
    animation-iteration-count: 1;
    position: relative;
  }
}
@keyframes Payment-animation {
  0% {
    left: 100%;
  }
  100% {
    left: 0%;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .paypal-logo {
    animation: Payment-credit-animation 1 .5s;
    animation-iteration-count: 1;
    position: relative;
  }
}

@keyframes Payment-credit-animation {
  0% {
    left: 100%;
  }
  100% {
    left: 0%;
  }
}


@media (prefers-reduced-motion: no-preference) {
  .crypto-payment {
    animation: Payment-crypto-animation 1 .5s;
    animation-iteration-count: 1;
    position: relative;
  }
}

@keyframes Payment-crypto-animation {
  0% {
    left: 50%;
  }
  100% {
    left: 0%;
  }
}


